import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from './views/Dashboard.vue'

// product components
import ProductsMain from './views/Products/Index.vue'
import NewProduct from './views/Products/New.vue'
import Product from './views/Products/Product.vue'
// inventory components
import InventoryMain from './views/Inventory/Index.vue'
import NewStockAdjustment from './views/Inventory/New.vue'

Vue.use(Router)

import axios from "axios";

export default new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
            path: '/',
            name: 'dashboard',
            component: Dashboard
        },
        // product routes
        {
            path: "/products",
            name: "products",
            component: ProductsMain
        },
        {
            path: "/products/new",
            name: "newProduct",
            component: NewProduct
        },
        { 
            path: '/products/:id',
            component: Product, 
            props: true 
        },
        // inventory routes
        {
            path: "/inventory",
            name: "inventory",
            component: InventoryMain
        },
        {
            path: "/inventory/new",
            name: "new stock adjustment",
            component: NewStockAdjustment
        }
    ],
    methods: {
        setData (err, product) {
            if (err) {
                this.error = err.message
            } else {
                this.product = product
            }
        }
    }
})
